.btn-close{
  width: 40px;
  height: 40px;
  position: absolute;
  top: -40px;
  right: -40px;
  border: none;
  outline: none;
  background-color: transparent;
  background-image: url('../../images/close-icon.svg');
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transform: rotate(45deg);
  transition: opacity .3s;
  padding: 0;
}

.btn-close:hover{
  opacity: 0.6;
}